.navbar {
  background: #ffffff; 
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  font-family: 'Avenir Black', 'Roboto', Helvetica, Arial, sans-serif; 
  display: flex;
  justify-content: space-around;
  align-items: center; 
  right: 0;
  top: 0; 
  z-index: 15;
  background-color: transparent; 
} 

.navbar-logo {
  color: #fff; 
  display: flex; 
  justify-content: center; 
  /* margin-left: 20px; */
  cursor: pointer;
  text-decoration: none;
  margin-top: 4rem;
  /* font-size: 2rem; */
}

.m-line {
  display: none; 
}

.fa-firstdraft {
  margin-left: 0.5rem;
  font-size: 1.6rem;
}

.nav-menu {
  display: grid;
  background: #ffffff; 
  grid-template-columns: repeat(7, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 100vw;
  justify-content: center;
  margin-right: 0.5rem; 
} 

.nav-menu-mandarin {
  display: grid;
  background: #ffffff;
  grid-template-columns: repeat(7, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 100vw;
  justify-content: center; 
}

.mobile-logo {
  display: none; 
}

.nav-item {
  display: flex; 
  align-items: center;
  height: 80px; 
  margin-top: 3.5rem; 
}
.nav-item-1 {
  display: flex; 
  align-items: center;
  height: 80px; 
  margin-top: 3.5rem; 
}
.nav-item-2 {
  display: none; 
}

.nav-item-move {
  display: flex; 
  justify-content: center; 
  height: 80px; 
  margin-top: 4.7rem; 
}

.nav-links {
  color: rgb(0, 0, 0);
  text-align: center; 
  text-decoration: none;
  padding: 1rem 1.5rem;
  margin-top: 4rem;
  text-transform: uppercase;
  font-weight: bolder; 
  font-family: 'Avenir Black', 'Roboto', Helvetica, Arial, sans-serif; 
}

.nav-links:hover {
  background-color: #1e3272;
  color: #fff; 
  border-radius: 4px;
  transition: all 0.2s ease-out;
} 

.shopping-cart {
  margin-top: 4.4rem; 
  align-items: center;
  padding: 0.5rem 1rem; 
  text-align: center; 
  color: black; 
}

.nav-item-2 {
  display: flex;
  align-items: center;
  height: 80px;
}

.nav-links-2 {
  color: rgb(0, 0, 0);
  text-decoration: none;
  padding: 0.5rem 1rem;
}

.nav-links-2:hover {
  background-color: #1e3272;
  color: #fff; 
  border-radius: 4px;
  transition: all 0.2s ease-out;
}

.nav-item-3 {
  display: flex;
  align-items: center;
  height: 80px;
}

.nav-links-3 {
  color: rgb(0, 0, 0);
  text-decoration: none;
  padding: 0.5rem 1rem;
}

.nav-links-3:hover {
  background-color: #1e3272;
  color: #fff; 
  border-radius: 4px;
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
} 

@media screen and (width: 412px) and (max-height: 914px){ 
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex; 
    justify-content: center;
    flex-direction: column;
    width: 110%;
    height: 80vh;
    position: absolute;
    top: 35px;
    left: -130%;
    opacity: 1;
    transition: all 0.5s ease;
  } 

  .logo {
    display: none; 
  }

  .nav-menu.active {
    background: #ffffff;
    left: -10%;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 10;
  }

  .nav-links {
    text-align: center;
    padding: 1rem;
    width: 100%;
    display: table; 
    z-index: 10;
  }

  .nav-links:hover {
    background-color: #1e3272;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .nav-links {
    text-align: center; 
    text-decoration: none;
    margin-top: 1rem;
    text-transform: uppercase;
    font-weight: bolder;
  }

  .fa-bars {
    color: rgb(0, 0, 0) !important; 
    font-size: 2rem;
  }

  .fa-times {
    color: rgb(0, 0, 0) !important; 
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    padding: 0;
    margin: 0;
    border-radius: 4px;
    width: 100%;
    background: #1e3272;
    text-decoration: none;
    color: #fff;
    font-size: 1.5rem;
    z-index: 10;
  } 

  .nav-links-mobile:hover {
    background: #fff;
    color: #1e3272;
    transition: 250ms;
  }

  .nav-item {
    margin-top: 0rem; 
    z-index: 10;
  }

  button {
    display: none;
  } 
}

