@media(max-width:1100px){ 
  .mobile-logo {
    display: block; 
    margin-left: 14.65vw;     
  }
  .nav-item-1 {
    display: none; 
  }
  .nav-item-2 {
    display: flex; 
    align-items: center;
    color: white; 
  }
  .active-1{
    display: none; 
  }
  .active-2{
    display: flex; 
    font-size: 1.2rem; 
    background-color: transparent;
    transition: all ease 0.2s; 
    color: rgb(255, 255, 255);
    text-align: center;
    text-decoration: none;
    padding: 1rem 1.5rem;
    margin-top: 0.2rem;
    text-transform: uppercase;
    font-weight: bolder;
  } 
      .menu-icon {
        display:block;
        }
      nav{
          justify-content: space-between;
          height: 65px;
      }
      .logo img{
          width:70px;
      }
      .menu{
          display:none;
          position:absolute;
          top:100px;
          left:0px;
          background-color:#212529;
          border-bottom:4px solid #da3a3a;
          width:100vw;
          padding:0px;
          margin:0px;
      }
      .menu li{
          width:100%;
      }
      nav .menu li a{
          width:100%;
          height:40px;
          color: white; 
          justify-content: center;
          align-items: center;
          margin:0px;
          padding: 25px; 
      }
      nav .menu-icon{
          cursor:pointer;
          float:right;
          padding:1px 1px;
          position:relative;
          user-select: none;
      }
      nav .menu-icon .nav-icon{
          background-color:#000000;
          position: absolute;
          display:block;
          height:2px;
          position:relative;
          /* transition: background 0.2s ease-out; */
          width:18px;
      }
      nav .menu-icon .nav-icon:before,
      nav .menu-icon .nav-icon:after{
          background:#000000; 
          content:'';
          display:block;
          height:100%;
          position:absolute;
          transition:all ease-out 0.2s;
          width:100%;
      }
      nav .menu-icon .nav-icon:before{
          top:5px;
      }
      nav .menu-icon .nav-icon:after{
          top:-5px;
      }
      nav .menu-btn:checked ~ .menu-icon .nav-icon{
          background:transparent;
      }
      nav .menu-btn:checked ~ .menu-icon .nav-icon:before{
          transform: rotate(-45deg);
          top:0;
      }
      nav .menu-btn:checked ~ .menu-icon .nav-icon:after{
          transform: rotate(45deg);
          top:0;
      }
      nav .menu-btn{
          display:none;
      }
      nav .menu-btn:checked ~ .menu{
          display:block;
      }
    
}
